.cards{
    margin-top: 60px;
}
.plus-section{   
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
    color: $secondary-color;
}
.name-section{
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $primary-color;
}
.products-description{
    display: flex;
    justify-content: center;
    text-decoration: none;
}
.list-description{
    list-style: none;
}
.product--name{
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #ffff;
}
.product--description{
    text-align: center;
    letter-spacing: .04em;
    color: #fff;
    padding: 20%;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 152%;  
}
.image {
    position: absolute;
    right: -23px;
    top: -200px;
}
.point{
    color: #fff;
    justify-content: center;
    font-size: 50px;
    display: flex;
}
.img-pink{
    background-color:$secondary-color;
    border-radius: 24px;
    margin-bottom: 200px;
    padding-top: 150px;
    display: flex;
    position: relative;
    bottom: -200px;
}
.img-brown{
    @extend .img-pink;
    background-color: $fifth-color;

}
.img-green{
    @extend .img-pink;
    background-color: $fourth-color;
}
.btn {
    position: relative;
    left: 116px;
    fill: #d41443;
    border: 2px;
    border-radius: 100%;
    margin-bottom: 40px;
    padding: 15px;
    background-color: #ffff;
}
@media screen and (min-width: 768px){
    .product{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .plus-section{
        font-size: 26px;
        padding-bottom: 18px;
    }
    .name-section{
        font-size: 48px;
        }
    .section-title{
    display: flex;
    flex-direction: column;
    }
    .container{
        width: 768px;
        display: flex;
        justify-content: center; 
        flex-direction: column;
    }
    .list{
        display: flex;
        justify-content: space-between ;
        width: 224px;
    }
    .image{
        width: 220px;
        height: 224px;
        right: 0px;
        top: -120px        }
        .img-pink:not(:last-child):not(:last-child) {
            margin-right: 16px;
        }      
        .list-card{
            display: flex;
            flex-wrap: nowrap;
        }  
        .btn{
            left: 90px;
        }
}
@media screen and (min-width: 1200px){
    .product{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .plus-section{
        font-size: 30px;
        padding-bottom: 16px;
    }
    .name-section{
        font-size: 58px;

    }
    
    .section-title{
    display: flex;
    flex-direction: column;
    }
    .container{
        width: 1200px;
        display: flex;
        justify-content: center; 
        flex-direction: column;
    }
    .list{
        display: flex;
        justify-content: space-between ;
        width: 368px;
    }
    .image{
        width: 368px;
        height: 394px;
        right: 0px;   
        top: -250px;
        }
        .img-pink:not(:last-child):not(:last-child) {
            margin-right: 32px;
        }      
        .list-card{
            position: relative;
            display: flex;
            flex-wrap: nowrap;
            top:100px;
        }  
        .btn{
            left: 160px;
        }
        .product--name{
            font-size: 30px;
            line-height: 34.35px;
            font-family: "Titan One";
            font-weight: 400px;

        }
        .product--description{
            font-size:16px;
            font-family: "DDM Sans";
            font-weight: 700;
            line-height: 28px;
            

        }
}
